import { Button } from "@mui/material";
import React from "react";
import { useEffect } from "react";
import { Spinner } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { getOneorder, getShippingAddress } from "../redux/action";
import Header from "./Header";
import logo from "../images/photo.jpg";
import Swal from "sweetalert2";
function OneOrder() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { _id } = useParams();
  const { data, loading, error } = useSelector((state) => state.oneorder);
  useEffect(() => {
    dispatch(getOneorder(_id));
  }, []);
  const dispatch1 = useDispatch();
  const { data1, loading1 } = useSelector((state) => state.shippingAddress);
  useEffect(() => {
    dispatch1(getShippingAddress(_id));
  }, []);
  if (loading === true) {
    <Header />;
  } else {
    {
      error &&
        Swal.fire({
          icon: "error",
          text: error,
        });
    }
  }
  return (
    <div>
      <Header />
      <div style={{ display: "grid", justifyContent: "center" }}>
        {loading && loading1 ? (
          <Spinner animation="border" variant="primary" />
        ) : (
          <div>
            <form className="container rounded bg-white mt-5">
              <div className="row">
                <div className="col-md-4 border-right">
                  <div className="d-flex flex-column align-items-center text-center p-3 py-5">
                    <img
                      className="rounded-circle mt-5"
                      src={logo}
                      width="350"
                      alt="photo_profile"
                    />
                    <span className="font-weight-bold">
                      {/* {show.firstname} {show.lastname} */}
                    </span>
                    <span className="text-black-50">{/* {show.email} */}</span>
                    <span>{/* {show.city} */}</span>
                  </div>
                </div>
                <div className="col-md-8">
                  <div className="p-3 py-5">
                    <div className="d-flex justify-content-between align-items-center mb-3">
                      <div className="d-flex flex-row align-items-center back"></div>
                    </div>
                    <div className="row mt-2">
                      <div className="col-md-6">
                        <label className="text-muted">Order ID</label>
                        <input
                          type="text"
                          className="form-control"
                          style={{ width: "100%", marginRight: "300px" }}
                          placeholder="Order ID"
                          defaultValue={data._id}
                        />
                      </div>
                      <div className="col-md-6">
                        <label className="text-muted">PaymentMethod</label>
                        <input
                          type="text"
                          className="form-control"
                          defaultValue={data.paymentMethod}
                          style={{ width: "100%" }}
                          placeholder="PaymentMethod"
                        />
                      </div>
                    </div>
                    <div className="row mt-3">
                      <div className="col-md-6">
                        <label className="text-muted">Address</label>
                        <input
                          type="text"
                          className="form-control"
                          style={{ width: "100%" }}
                          placeholder="Address"
                          defaultValue={data1.address}
                        />
                      </div>
                      <div className="col-md-6">
                        <label className="text-muted">Mobile</label>
                        <input
                          type="text"
                          className="form-control"
                          defaultValue={data1.phone}
                          style={{ width: "100%" }}
                          placeholder="Phone number"
                        />
                      </div>
                    </div>
                    <div className="row mt-3">
                      <div className="col-md-6">
                        <label className="text-muted">PostalCode</label>
                        <input
                          type="text"
                          className="form-control"
                          style={{ width: "100%" }}
                          placeholder="PostalCode"
                          defaultValue={data1.postalCode}
                        />
                      </div>
                      <div className="col-md-6">
                        <label className="text-muted">City</label>
                        <input
                          type="text"
                          className="form-control"
                          defaultValue={data1.city}
                          style={{ width: "100%" }}
                          placeholder="City"
                        />
                      </div>
                    </div>
                    <div className="row mt-3">
                      <div className="col-md-6">
                        <label className="text-muted">ShippingPrice</label>
                        <input
                          type="text"
                          className="form-control"
                          style={{ width: "100%" }}
                          placeholder="ShippingPrice"
                          defaultValue={data.shippingPrice}
                        />
                      </div>
                      <div className="col-md-6">
                        <label className="text-muted">TotalPrice</label>
                        <input
                          type="text"
                          className="form-control"
                          defaultValue={data.totalPrice}
                          style={{ width: "100%" }}
                          placeholder="TotalPrice"
                        />
                      </div>
                    </div>
                    <div className="mt-5 text-right">
                      <Button
                        style={{ position: "100px" }}
                        onClick={() => navigate("/")}
                        variant="contained"
                        className="btn btn-primary profile-button"
                      >
                        Back To Home
                      </Button>
                    </div>
                  </div>
                </div>
              </div>
            </form>
          </div>
        )}
      </div>
    </div>
  );
}

export default OneOrder;
