import { createContext } from "react";
import { getProductData } from "./ProductTest";
import useLocalStorage from "./hook/useLocalStorage";
export const CartContext = createContext({
  items: [],
  getProductQuantity: () => {},
  addOneToCart: () => {},
  removeOneFromCart: () => {},
  deleteFormCart: () => {},
  getTotalCost: () => {},
});
export function CartProvider({ children }) {
  const [cartProducts, setCartProducts] = useLocalStorage("cartProducts", []);

  // [ { id: 1 , countInStock: 3 }, { id: 2, countInStock: 1 } ]

  function getProductQuantity(_id) {
    const countInStock = cartProducts.find(
      (product) => product._id === _id
    )?.countInStock;

    if (countInStock === undefined) {
      return 0;
    }

    return countInStock;
  }

  function addOneToCart(_id) {
    const countInStock = getProductQuantity(_id);

    if (countInStock === 0) {
      // product is not in cart
      setCartProducts([
        ...cartProducts,
        {
          _id: _id,
          countInStock: 1,
        },
      ]);
    } else {
      // product is in cart
      // [ { _id: 1 , countInStock: 3 }, { _id: 2, countInStock: 1 } ]    add to product _id of 2
      setCartProducts(
        cartProducts.map(
          (product) =>
            product._id === _id // if condition
              ? { ...product, countInStock: product.countInStock + 1 } // if statement is true
              : product // if statement is false
        )
      );
    }
  }

  function removeOneFromCart(_id) {
    const countInStock = getProductQuantity(_id);

    if (countInStock === 1) {
      deleteFromCart(_id);
    } else {
      setCartProducts(
        cartProducts.map(
          (product) =>
            product._id === _id // if condition
              ? { ...product, countInStock: product.countInStock - 1 } // if statement is true
              : product // if statement is false
        )
      );
    }
  }

  function deleteFromCart(_id) {
    // [] if an object meets a condition, add the object to array
    // [product1, product2, product3]
    // [product1, product3]
    setCartProducts((cartProducts) =>
      cartProducts.filter((currentProduct) => {
        return currentProduct._id !== _id;
      })
    );
  }

  function getTotalCost() {
    let totalCost = 0;
    cartProducts.map((cartItem) => {
      const productData = getProductData(cartItem._id);
      totalCost += productData.price * cartItem.countInStock;
    });
    return totalCost;
  }

  const contextValue = {
    items: cartProducts,
    getProductQuantity,
    addOneToCart,
    removeOneFromCart,
    deleteFromCart,
    getTotalCost,
  };

  return (
    <CartContext.Provider value={contextValue}>{children}</CartContext.Provider>
  );
}

export default CartProvider;
