import axios from "axios";
import React, { useRef, useState } from "react";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import Header from "./Header";
import { Alert } from "react-bootstrap";
const url = "http://kzico.runflare.run/user/signup";
function SignUp() {
  const navigate = useNavigate();
  /* useEffect(() => {
    if (localStorage.getItem("post")) {
      navigate("/");
    } 
  }, []); */
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmpassword, setConfirmpassword] = useState("");
  const [mobile, setMobile] = useState("");
  const emailRef = useRef();
  const passwordRef = useRef();
  const passwordConfirmRef = useRef();
  const [error, setError] = useState("");
  const [showp, setShowp] = useState(false);
  const [showc, setShowc] = useState(false);
  const handleShowp = () => {
    setShowp(!showp);
  };
  const handleShowc = () => {
    setShowc(!showc);
  };
  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      if (passwordRef.current.value !== passwordConfirmRef.current.value) {
        return setError("Password do not match");
      }
      const res = await axios.post(url, {
        username: username,
        email: email,
        password: password,
        confirmpassword: confirmpassword,
        mobile: mobile,
      });

      Swal.fire({
        icon: "success",
        text: "Successfully Sign Up!",
      });

      navigate("/login");
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: error.response.data.message,
      });
    }
  };
  return (
    <>
      <Header />
      <div className="sign--body">
        <div className="box-S">
          <form onSubmit={handleSubmit} className="form-S">
            <h2>Sign up</h2>
            {error && <Alert variant="danger">{error}</Alert>}
            <div className="inputBox-S">
              <input
                type="text"
                onChange={(e) => setUsername(e.target.value)}
                value={username}
                required="required"
              />
              <span>Username</span>
              <i />
            </div>
            <div className="inputBox-S">
              <input
                type="email"
                onChange={(e) => setEmail(e.target.value)}
                value={email}
                ref={emailRef}
                required="required"
              />
              <span>Email</span>
              <i />
            </div>
            <div className="inputBox-S">
              <input
                type={showp ? "text" : "password"}
                onChange={(e) => setPassword(e.target.value)}
                value={password}
                ref={passwordRef}
                required="required"
              />
              <label className="label-password" onClick={handleShowp}>
                {showp ? "Hide" : "Show"}
              </label>
              <span>Password</span>
              <i />
            </div>
            <div className="inputBox-S">
              <input
                type={showc ? "text" : "password"}
                onChange={(e) => setConfirmpassword(e.target.value)}
                value={confirmpassword}
                ref={passwordConfirmRef}
                required="required"
              />

              <span>Confirm Password</span>
              <label className="label-password" onClick={handleShowc}>
                {showc ? "Hide" : "Show"}
              </label>
              <i />
            </div>
            <div className="inputBox-S">
              <input
                type="text"
                onChange={(e) => setMobile(e.target.value)}
                value={mobile}
                required="required"
              />
              <span>Mobile</span>
              <i />
            </div>

            <button type="submit" onClick={handleSubmit} className="Sign-up">
              Sign up
            </button>
          </form>
        </div>
      </div>
    </>
  );
}

export default SignUp;
