import React, { useEffect, useContext } from "react";
import { Spinner } from "react-bootstrap";
import { Button, Grid, Container, Typography } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { getOneProduct } from "../redux/action";

import { CartContext } from "./CartContext";
import Header from "./Header";
import { width } from "@mui/system";
function OneProduct() {
  const navagate = useNavigate();
  const dispatch = useDispatch();
  const { _id } = useParams();
  const { data, loading } = useSelector((state) => state.oneproduct);

  const cart = useContext(CartContext);
  const productQuantity = cart.getProductQuantity(_id);
  useEffect(() => {
    dispatch(getOneProduct(_id));
  }, []);

  return (
    <>
      <Header />
      <div style={{ display: "grid", justifyContent: "center" }}>
        {loading ? (
          <Spinner animation="border" variant="primary" />
        ) : (
          <Container className="product-one" style={{ marginTop: "50px" }}>
            <Grid container spacing={4}>
              <Grid item xs={8} md={6} className="image-wrapper">
                <img src={data.image} alt={`PhotoWebInfinity`} />
              </Grid>
              <Grid item xs={12} md={6} className="text">
                <Typography variant="h3">{data.name}</Typography>
                <Typography variant="h5" style={{ color: "#5A6268" }}>
                  description: {data.description}
                  <div style={{ color: "#518adb", marginTop: "5px" }}>
                    Brand: {data.brand}
                  </div>
                </Typography>
                <Typography
                  variant="h5"
                  style={{ color: "#FFCA2C", marginTop: "10px" }}
                >
                  Rating: {data.rating}
                </Typography>
                <Typography
                  variant="h6"
                  style={{ color: "#157347", marginTop: "10px" }}
                >
                  Price: ${data.price}
                  <div style={{ color: "#5A6268", marginTop: "5px" }}>
                    Color: {data.color}
                  </div>
                </Typography>
                <Grid container spacing={4}>
                  <>
                    {productQuantity > 0 ? (
                      <div>
                        <Grid item xs={4}>
                          <Button
                            size="small"
                            variant="contained"
                            style={{ marginTop: "30px" }}
                            className="increase-product-quntity"
                            disabled={data.countInStock === 0}
                            onClick={() => cart.addOneToCart(_id)}
                          >
                            +
                          </Button>
                        </Grid>
                        <Grid item xs={9}>
                          <Typography
                            style={{ width: "100px" }}
                            className="quantity"
                            variant="h6"
                          >
                            Qunatity: {productQuantity}
                          </Typography>
                        </Grid>
                        <Grid item xs={4}>
                          <Button
                            size="small"
                            variant="contained"
                            color="error"
                            style={{ margin: "0" }}
                            disabled={data.countInStock === 0}
                            onClick={() => cart.removeOneFromCart(_id)}
                          >
                            -
                          </Button>
                        </Grid>
                      </div>
                    ) : (
                      <Grid item xs={12}>
                        <Button
                          size="large"
                          variant="outlined"
                          color="success"
                          style={{ marginTop: "100px" }}
                          className="custom-button"
                          disabled={data.countInStock === 0}
                          onClick={() => cart.addOneToCart(_id)}
                        >
                          Add to Cart
                        </Button>
                      </Grid>
                    )}
                  </>
                  <Grid item xs={12}>
                    <Button
                      size="large"
                      variant="outlined"
                      color="success"
                      className="custom-button"
                      onClick={() => navagate("/cart")}
                    >
                      go to Cart
                    </Button>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Container>
        )}
      </div>
    </>
  );
}

export default OneProduct;
