import { Card } from "react-bootstrap";
import { CartContext } from "./CartContext";
import { useContext } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "@mui/material";
import { useState } from "react";
import Model from "./Model";

function ProductCard(props) {
  const navagte = useNavigate();
  const product = props.product;
  const cart = useContext(CartContext);
  const productQuantity = cart.getProductQuantity(product._id);
  const [model, setModel] = useState(false);
  const [tempdata, setTempdata] = useState([]);
  const getData = (
    image,
    name,
    brand,
    price,
    rating,
    color,
    category,
    countInStock
  ) => {
    let tempData = [
      image,
      name,
      brand,
      price,
      rating,
      color,
      category,
      countInStock,
    ];
    setTempdata((item) => [1, ...tempData]);

    return setModel(true);
  };

  return (
    <>
      <Card className="card">
        <Card.Img className="card-img" variant="top" src={product.image} />
        <div className="card-body">
          <h6 className="card-title">{product.name}</h6>
          <p
            className="card-sub-title"
            style={{ color: "#0B5ED7", textAlign: "center" }}
          >
            Available: {product.countInStock}
          </p>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <p className="card-info" style={{ color: "#51eb27" }}>
              Price: ${product.price}
            </p>
            <p className="card-info" style={{ color: "#FFE000" }}>
              Rating: {product.rating}
            </p>
          </div>
          <>
            {productQuantity > 0 ? (
              <div
                className="d-flex align-items-center flex-column"
                style={{ gap: ".5rem" }}
              >
                <div
                  className="d-flex align-items-center justify-content-center"
                  style={{ gap: ".5rem" }}
                >
                  <Button
                    size="small"
                    variant="contained"
                    style={{ background: "#FF3131" }}
                    onClick={() => cart.removeOneFromCart(product._id)}
                  >
                    -
                  </Button>
                  <div>
                    <span className="fs-3">{productQuantity}</span>
                  </div>
                  <Button
                    size="small"
                    variant="contained"
                    onClick={() => cart.addOneToCart(product._id)}
                  >
                    +
                  </Button>
                </div>
                {/*  <Button
                size="small"
                variant="contained"
                style={{ background: "#FF3131" }}
                onClick={() => cart.deleteFromCart(product._id)}
              >
                Remove
              </Button> */}
              </div>
            ) : (
              <Button
                style={{
                  width: "100%",
                  background: "linear-gradient(to right,#373B44,#2B72E6)",
                  textTransform: "capitalize",
                }}
                disabled={product.countInStock === 0}
                variant="contained"
                onClick={() => cart.addOneToCart(product._id)}
              >
                Buy
              </Button>
            )}
          </>
          <Button
            style={{
              marginTop: "5px",
              width: "100%",
              background: "linear-gradient(to right,#373B44, #FF2D2D)",
              textTransform: "capitalize",
            }}
            variant="contained"
            onClick={() => navagte(`/${product._id}`)}
            /*   onClick={() =>
              getData(
                product.image,
                product.name,
                product.price,
                product.brand,
                product.rating,
                product.color,
                product.category,
                product.countInStock
              )
            } */
          >
            More Delites
          </Button>
        </div>
      </Card>
      {model === true ? (
        <Model
          image={tempdata[1]}
          name={tempdata[2]}
          price={tempdata[3]}
          brand={tempdata[4]}
          rating={tempdata[5]}
          color={tempdata[6]}
          category={tempdata[7]}
          countInStock={tempdata[8]}
          hide={() => setModel(false)}
        />
      ) : (
        ""
      )}
    </>
  );
}

export default ProductCard;
