import React from "react";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

function Protected(props) {
  let Cma = props.Cma;
  const navigate = useNavigate();
  useEffect(() => {
    if (!localStorage.getItem("post")) {
      navigate("/login");
    }
  }, []);
  return (
    <div>
      <Cma />
    </div>
  );
}

export default Protected;
