import React from "react";

function Model(props) {
  let modelStyle = {
    display: "block",
    backgroundColor: "rgba(0,0,0,0.8)",
  };
  /*   const dispatch = useDispatch();
  const { _id } = useParams();
  const { data } = useSelector((state) => state.oneproduct);
  console.log(_id);
  useEffect(() => {
    dispatch(getOneProduct(_id));
  }, []); */
  return (
    <div className="modal show fade" style={modelStyle}>
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">{props.name}</h5>
            <button
              type="button"
              className="btn-close"
              onClick={props.hide}
            ></button>
          </div>
          <div className="modal-body">
            <img
              src={props.image}
              style={{ width: "100px", height: "200px", marginLeft: "180px" }}
            />
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <p style={{ color: "#157347", marginTop: "20px" }}>
                Price: ${props.price}
              </p>
              <p style={{ color: "#FF0303", marginTop: "20px" }}>
                Brand: {props.brand}
              </p>
              <p style={{ color: "#FFCA2C", marginTop: "20px" }}>
                Rating: {props.rating}
              </p>
              <p style={{ color: "#5A6268", marginTop: "20px" }}>
                Color: {props.color}
              </p>
            </div>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <p style={{ color: "#5C636A", marginTop: "5px" }}>
                Category: {props.category}
              </p>
              <p style={{ color: "#1565C0", marginTop: "5px" }}>
                Quantity: {props.countInStock}
              </p>
            </div>
            {/* <div>Description: {data.description}</div> */}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Model;
