import { React, useContext } from "react";
import logo from "../images/logo.png";
import carts from "../images/cart.svg";
import search from "../images/search-icon.svg";

import navtoggle from "../images/nav-toggle.svg";
import { useNavigate } from "react-router-dom";

import { Nav, Navbar, NavDropdown } from "react-bootstrap";
import { CartContext } from "./CartContext";
import { Cookies } from "react-cookie";
import { useEffect } from "react";
const Header = () => {
  const navigate = useNavigate();
  const cart = useContext(CartContext);

  const productCount = cart.items.reduce(
    (sum, product) => sum + product.countInStock,
    0
  );

  let user = JSON.parse(localStorage.getItem("post"));
  const Cookie = new Cookies();
  function logout() {
    localStorage.clear();
    Cookie.remove("token");
    navigate("/");
  }
  useEffect(() => {
    const navMenu = document.getElementById("nav-menu"),
      navToggle = document.getElementById("nav-toggle");

    if (navToggle) {
      navToggle.addEventListener("click", () => {
        navMenu.classList.toggle("show-menu");
      });
    }
  }, []);

  return (
    <Navbar
      sticky="top"
      className="header container "
      style={{ background: "white" }}
    >
      <Nav className="nav" style={{ marginTop: "20px" }}>
        <div className="nav-header">
          <a href="/" className="nav--logo">
            <img src={logo} alt="" />
          </a>
          <div className="nav--toggle" id="nav-toggle">
            <img src={navtoggle} alt="" />
          </div>
        </div>

        <div className="nav--menu" id="nav-menu">
          {localStorage.getItem("post") ? (
            <>
              <ul className="nav--list">
                <li
                  className="nav--link active--link"
                  onClick={() => navigate("/")}
                >
                  Home
                </li>
                <li
                  className="nav--link "
                  onClick={() => navigate("/services")}
                >
                  Services
                </li>
                <li className="nav--link" onClick={() => navigate("/about")}>
                  About
                </li>
                <li className="nav--link" onClick={() => navigate("/contact")}>
                  Contact
                </li>
              </ul>
              <form action="" className="nav--search-form">
                <span className="search--icon">
                  <img src={search} alt="" />
                </span>
                <input
                  type="text"
                  placeholder="Search"
                  className="nav--input"
                />
                <span
                  className="cart--icon"
                  cart-items={productCount}
                  onClick={() => navigate("/cart")}
                >
                  <img src={carts} alt="" />
                </span>
              </form>
            </>
          ) : (
            <>
              <ul className="nav--list">
                <li
                  className="nav--link active--link"
                  onClick={() => navigate("/")}
                >
                  Home
                </li>
                <li
                  className="nav--link "
                  onClick={() => navigate("/services")}
                >
                  Services
                </li>
                <li className="nav--link" onClick={() => navigate("/about")}>
                  About
                </li>
                <li className="nav--link" onClick={() => navigate("/contact")}>
                  Contact
                </li>
              </ul>
              <form action="" className="nav--search-form">
                <span className="search--icon">
                  <img src={search} alt="" />
                </span>
                <input
                  type="text"
                  placeholder="Search"
                  className="nav--input"
                />
                <span
                  className="cart--icon"
                  cart-items={productCount}
                  onClick={() => navigate("/cart")}
                >
                  <img src={carts} alt="" />
                </span>
              </form>
              <span>
                <button className="log--in" onClick={() => navigate("/login")}>
                  Log in
                </button>
                <button
                  className="sign--up"
                  onClick={() => navigate("/signup")}
                >
                  Sign up
                </button>
              </span>
            </>
          )}
        </div>
        <Nav className="account-responsive">
          {localStorage.getItem("post") ? (
            <NavDropdown title={user}>
              <NavDropdown.Item onClick={() => navigate("/user/profile")}>
                MyProfile
              </NavDropdown.Item>
              <NavDropdown.Item onClick={() => navigate("/orders")}>
                Orders
              </NavDropdown.Item>
              <NavDropdown.Item
                onClick={() => navigate("/setting/changeProfile")}
              >
                Setting
              </NavDropdown.Item>
              <NavDropdown.Item onClick={logout}>LogOut</NavDropdown.Item>
            </NavDropdown>
          ) : null}
        </Nav>
      </Nav>
    </Navbar>
  );
};

export default Header;
