import Header from "./Header";
import { CartContext } from "./CartContext";
import { React, useContext } from "react";
import { Button } from "@mui/material";
import CartProduct from "./CartProduct";

import { useNavigate } from "react-router-dom";

function Cart() {
  const navgate = useNavigate();
  const cart = useContext(CartContext);

  const productCount = cart.items.reduce(
    (sum, product) => sum + product.countInStock,
    0
  );

  return (
    <>
      <Header />
      <div>
        <div closeButton>
          <div style={{ fontSize: "20px", textAlign: "center" }}>
            <span style={{ color: "#2B72E6" }}>Shopping</span>{" "}
            <span style={{ color: "#FF3232" }}>Infinty </span>
            <span style={{ color: "black" }}>Cart</span>
          </div>
        </div>
        <div>
          {productCount > 0 ? (
            <>
              <div className="order-total-1">
                <p>Items In Your Cart</p>
              </div>

              {cart.items.map((currentProduct, idx) => (
                <CartProduct
                  key={idx}
                  _id={currentProduct._id}
                  countInStock={currentProduct.countInStock}
                ></CartProduct>
              ))}
              <div
                className="order-card"
                style={{ display: "flex", justifyContent: "space-between" }}
              >
                <h1>Total: {cart.getTotalCost().toFixed(2)}</h1>

                <Button
                  variant="contained"
                  color="success"
                  className="place-order-btn"
                  onClick={() => navgate("/address")}
                >
                  NEXT
                </Button>
              </div>
            </>
          ) : (
            <h1 style={{ textAlign: "center", marginTop: "200px" }}>
              There are no items in your cart!
            </h1>
          )}
        </div>
      </div>
    </>
  );
}

export default Cart;
