import { Button } from "@mui/material";
import axios from "axios";
import React, { useState } from "react";

import { useEffect } from "react";
import { Cookies } from "react-cookie";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import Header from "./Header";

function Profile() {
  const navigate = useNavigate();
  const [show, setShow] = useState("");
  useEffect(() => {
    getprofile();
  }, []);
  const getprofile = async () => {
    const cookeis = new Cookies();
    const token = await cookeis.get("token");
    try {
      const { data } = await axios.get(
        "http://kzico.runflare.run/user/profile",

        {
          headers: {
            authorization: `Bearer ${token}`,
          },
        }
      );
      setShow(data.user);
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: error.response.data.message,
      });
    }
  };

  return (
    <div>
      <Header />
      <form onSubmit={getprofile} className="container rounded bg-white mt-5">
        <div className="row">
          <div className="col-md-4 border-right">
            <div className="d-flex flex-column align-items-center text-center p-3 py-5">
              <img
                className="rounded-circle mt-5"
                src={show.image}
                width="200"
                alt="photo_profile"
              />
              <span className="font-weight-bold">
                {show.firstname} {show.lastname}
              </span>
              <span className="text-black-50">{show.email}</span>
              <span>{show.city}</span>
            </div>
          </div>
          <div className="col-md-8">
            <div className="p-3 py-5">
              <div className="d-flex justify-content-between align-items-center mb-3">
                <div className="d-flex flex-row align-items-center back"></div>
              </div>
              <div className="row mt-2">
                <div className="col-md-6">
                  <label className="text-muted">FastName</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="FirstName"
                    defaultValue={show.firstname}
                  />
                </div>
                <div className="col-md-6">
                  <label className="text-muted">LastName</label>
                  <input
                    type="text"
                    className="form-control"
                    defaultValue={show.lastname}
                    placeholder="LastName"
                  />
                </div>
              </div>
              <div className="row mt-3">
                <div className="col-md-6">
                  <label className="text-muted">Email</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Email"
                    defaultValue={show.email}
                  />
                </div>
                <div className="col-md-6">
                  <label className="text-muted">Mobile</label>
                  <input
                    type="text"
                    className="form-control"
                    defaultValue={show.mobile}
                    placeholder="Phone number"
                  />
                </div>
              </div>
              <div className="row mt-3">
                <div className="col-md-6">
                  <label className="text-muted">Age</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Age"
                    defaultValue={show.age}
                  />
                </div>
                <div className="col-md-6">
                  <label className="text-muted">City</label>
                  <input
                    type="text"
                    className="form-control"
                    defaultValue={show.city}
                    placeholder="City"
                  />
                </div>
              </div>
              <div className="row mt-3">
                <div className="col-md-6">
                  <label className="text-muted">Usernmae</label>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Username"
                    defaultValue={show.username}
                  />
                </div>
                <div className="col-md-6">
                  <label className="text-muted">Gender</label>
                  <input
                    type="text"
                    className="form-control"
                    defaultValue={show.gender}
                    placeholder="Gender"
                  />
                </div>
              </div>
              <div className="mt-5 text-right">
                <Button
                  style={{ position: "100px" }}
                  onClick={() => navigate("/")}
                  variant="contained"
                  className="btn btn-primary profile-button"
                >
                  Back To Home
                </Button>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}

export default Profile;
