import axios from "axios";
import React, { useState } from "react";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import Header from "./Header";
import { Cookies } from "react-cookie";
const url = "http://kzico.runflare.run/user/login";
function LogIn() {
  const navigate = useNavigate();
  useEffect(() => {
    if (localStorage.getItem("post")) {
      navigate("/");
    }
  }, []);
  const navgite = useNavigate();
  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [show, setShow] = useState(false);
  const handleShow = () => {
    setShow(!show);
  };
  const handleSubmit = async (e) => {
    try {
      const res = await axios.post(url, {
        email: email,
        username: username,
        password: password,
      });

      localStorage.setItem("post", JSON.stringify(res.data.user.email));
      if (url) {
        const Cookie = new Cookies();
        Cookie.set("token", res.data.user.token);
      }
      Swal.fire({
        icon: "success",
        text: "Successfully Login In!",
      });

      navigate("/");
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: error.response.data.message,
      });
    }
  };

  return (
    <>
      <Header />
      <div className="login--body">
        <div className="box">
          <div onSubmit={handleSubmit} className="form">
            <h2>Log in</h2>
            <div className="inputBox">
              <input
                onChange={(e) =>
                  setEmail(e.target.value) || setUsername(e.target.value)
                }
                value={(username, email)}
                type="text"
                required="required"
                name={(username, email)}
              />
              <span> Email | Username </span>
              <i />
            </div>
            <div className="inputBox">
              <input
                onChange={(e) => setPassword(e.target.value)}
                value={password}
                type={show ? "text" : "password"}
                required="required"
                name={password}
              />
              <label className="label-password" onClick={handleShow}>
                {show ? "Hide" : "Show"}
              </label>
              <span>Password</span>
              <i />
            </div>
            <div className="links">
              <a className="a" href="#">
                Forgot Password
              </a>
              <a className="a" onClick={() => navgite("/signup")}>
                Sign up
              </a>
            </div>
            <button type="submit" className="login" onClick={handleSubmit}>
              Login
            </button>
          </div>
        </div>
      </div>
    </>
  );
}

export default LogIn;
