import { Button } from "@mui/material";
import React from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import Header from "./Header";
import { Cookies } from "react-cookie";
import axios from "axios";
import Swal from "sweetalert2";
function ChangePassword() {
  const [oldPassword, setOldPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const navigate = useNavigate();
  const changePassword = async (e) => {
    e.preventDefault();
    const cookeis = new Cookies();
    const token = await cookeis.get("token");
    try {
      const { data } = await axios.put(
        "http://kzico.runflare.run/user/change-password",
        {
          old_password: oldPassword,
          new_password: newPassword,
        },
        {
          headers: {
            authorization: `Bearer ${token}`,
          },
        }
      );
      Swal.fire({
        icon: "success",
        text: "Successfully ChangePassword",
      });
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: error.response.data.message,
      });
    }
  };
  return (
    <div>
      <Header />
      <div className="container-1">
        <div className="back-1">
          <Button
            className="button-e"
            variant="contained"
            onClick={() => navigate("/setting/changeProfile")}
          >
            Change Profile
          </Button>
          <Button
            className="button-e"
            style={{ background: "#e71d1d" }}
            variant="contained"
            onClick={() => navigate("/setting/changePassword")}
          >
            Change Password
          </Button>
          <Button
            className="button-e"
            style={{ background: "#f9b943" }}
            variant="contained"
            onClick={() => navigate("/setting/uploadAvatar")}
          >
            Upload Avatar
          </Button>
        </div>
        <div className="back-2" onSubmit={changePassword}>
          <div className="col-md-6">
            <input
              type="oldPassword"
              className="form-control"
              placeholder="Old Password"
              required="required"
              onChange={(e) => setOldPassword(e.target.value)}
              value={oldPassword}
              name={oldPassword}
            />
          </div>
          <div className="col-md-6">
            <input
              type="newPassword"
              className="form-control"
              placeholder="New Password"
              required="required"
              onChange={(e) => setNewPassword(e.target.value)}
              value={newPassword}
              name={newPassword}
            />
          </div>
          <Button onClick={changePassword} variant="contained">
            Save
          </Button>
        </div>
      </div>
    </div>
  );
}

export default ChangePassword;
