import { Button } from "@mui/material";
import React, { useState } from "react";
import { Cookies } from "react-cookie";
import axios from "axios";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import Header from "./Header";

const UploadAvatar = () => {
  const navigate = useNavigate();
  const [image, setImage] = useState(null);
  const handleImageChange = (e) => {
    setImage(e.target.files[0]);
  };
  const handleSubmit = async () => {
    try {
      const cookeis = new Cookies();
      const token = await cookeis.get("token");
      const formData = new FormData();
      formData.append("profile-image", image);
      const data = await axios.post(
        "http://kzico.runflare.run/user/profile-image",
        formData,
        {
          headers: {
            authorization: `Bearer ${token}`,
          },
        }
      );
      console.log(data);
      Swal.fire({
        icon: "success",
        text: "Successfully ChangeProfileImage",
      });
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: error.response.data.message,
      });
      console.log(error);
    }
  };

  return (
    <div>
      <Header />
      <div className="container-1">
        <div className="back-1">
          <Button
            className="button-e"
            variant="contained"
            onClick={() => navigate("/setting/changeProfile")}
          >
            Change Profile
          </Button>
          <Button
            className="button-e"
            style={{ background: "#e71d1d" }}
            variant="contained"
            onClick={() => navigate("/setting/changePassword")}
          >
            Change Password
          </Button>
          <Button
            className="button-e"
            style={{ background: "#f9b943" }}
            variant="contained"
            onClick={() => navigate("/setting/uploadAvatar")}
          >
            Upload Avatar
          </Button>
        </div>
        <form onSubmit={(e) => e.preventDefault()} className="back-2">
          <input type="file" onChange={handleImageChange} />
          <Button onClick={handleSubmit} type="submit" variant="contained">
            Upload Profile Image
          </Button>
        </form>
      </div>
    </div>
  );
};

export default UploadAvatar;
