import React from "react";
import { useEffect } from "react";
import Header from "./Header";
import { Spinner } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { getAllorder } from "../redux/action";
import Swal from "sweetalert2";
function AllOrders() {
  const dispatch = useDispatch();
  const navagte = useNavigate();
  const { data, loading, error } = useSelector((state) => state.allorder);
  useEffect(() => {
    dispatch(getAllorder());
  }, []);

  if (loading === false) {
    error &&
      Swal.fire({
        icon: "error",
        text: error,
      });
  }
  const d = new Date();
  return (
    <div>
      <Header />
      <div key={data._id}>
        {loading ? (
          <div style={{ display: "grid", justifyContent: "center" }}>
            <Spinner animation="border" variant="primary" />
          </div>
        ) : (
          <>
            {data.map((item) => (
              <div className="container my-5" key={item._id}>
                <div className="d-md-flex d-none justify-content-md-between justify-content-sm-center align-content-center border-bottom border-2 my-2 bg-dark text-light p-3 rounded-3">
                  <div className="col-2 text-sm-center text-md-start align-self-center">
                    <h1 style={{ color: "white" }} className="h5 fw-bold">
                      Order ID
                    </h1>
                  </div>
                  <div className="col-2 align-self-center">
                    <h1 style={{ color: "white" }} className="h5 fw-bold">
                      PaymentMethod
                    </h1>
                  </div>
                  <div className="col-3 align-self-center">
                    <h1 style={{ color: "white" }} className="h5 fw-bold">
                      Time
                    </h1>
                  </div>
                  <div className="col-2 align-self-center">
                    <h1 style={{ color: "white" }} className="h5 fw-bold">
                      Show details
                    </h1>
                  </div>
                  {/* <div className="col-2 align-self-center">
              <h1 style={{ color: "white" }} className="h5 fw-bold">
                Status
              </h1>
            </div> */}
                </div>
                <div className="d-md-flex d-sm-block justify-content-md-between justify-content-sm-center text-center border-bottom border-2 my-2 bg-light p-2 rounded-3">
                  <div className="col-md-2 text-sm-center text-md-start align-self-center my-2">
                    <h1 className="h6" style={{ marginRight: "10%" }}>
                      {item._id}
                    </h1>
                  </div>
                  <div className="col-md-2 text-sm-center text-md-start align-self-center my-2">
                    <h1 className="h6" style={{ marginLeft: "60%" }}>
                      {item.paymentMethod}
                    </h1>
                  </div>
                  <div className="col-md-3 text-sm-center text-md-start align-self-center my-2">
                    <h1 className="h6">{d.toLocaleDateString()}</h1>
                  </div>
                  <div className="col-md-2 text-sm-center text-md-start align-self-center my-2">
                    <a
                      className="btn btn-outline-dark w-100"
                      onClick={() => navagte(`/orders/${item._id}`)}
                    >
                      Details
                    </a>
                  </div>
                </div>
              </div>
            ))}
          </>
        )}
      </div>
    </div>
  );
}

export default AllOrders;
