import React from "react";
import { Route, Routes } from "react-router-dom";
import About from "./About";
import Cart from "./Cart";
import Contact from "./Contact";
import LogIn from "./LogIn";
import NotFound from "./NotFound";
import OneProduct from "./OneProduct";
import Services from "./Services";
import SignUp from "./SignUp";
import AllProduct from "./AllProduct";
import Protected from "./Protected";
import Protected2 from "./Protected2";
import Profile from "./Profile";
import Address from "./Address/Address";
import CheckOut from "./CheckOut";
import AllOrders from "./AllOrders";
import ChangeProfile from "./ChangeProfile";
import UploadAvatar from "./UploadAvatar";
import ChangePassword from "./ChangePassword";
import OneOrder from "./OneOrder";
/*import ProductCard from "./ProductCard"; */
function Router() {
  return (
    <Routes>
      <Route exact path="/" element={<AllProduct />} />
      <Route exact path="/:_id" element={<OneProduct />} />
      <Route exact path="/about" element={<About />} />
      <Route exact path="/contact" element={<Contact />} />
      <Route exact path="/services" element={<Services />} />
      <Route exact path="/user/profile" element={<Protected Cmp={Profile} />} />
      <Route
        exact
        path="/setting/changeProfile"
        element={<Protected Cmp={ChangeProfile} />}
      />
      <Route
        exact
        path="/setting/changePassword"
        element={<Protected Cmp={ChangePassword} />}
      />
      <Route
        exact
        path="/setting/uploadAvatar"
        element={<Protected Cmp={UploadAvatar} />}
      />
      <Route exact path="/cart" element={<Cart />} />
      <Route exact path="/address" element={<Protected2 Cma={Address} />} />
      <Route exact path="/checkout" element={<Protected Cmp={CheckOut} />} />
      <Route exact path="/orders" element={<Protected Cmp={AllOrders} />} />
      <Route exact path="/orders/:_id" element={<Protected Cmp={OneOrder} />} />
      <Route
        exact
        path="/setting/changeProfile"
        element={<Protected Cmp={ChangeProfile} />}
      />
      <Route exact path="/login" element={<LogIn />} />
      <Route exact path="/signup" element={<SignUp />} />
      <Route exact path="*" element={<NotFound />} />
    </Routes>
  );
}

export default Router;
