import { Button } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";
import Header from "./Header";
import { Cookies } from "react-cookie";
import axios from "axios";
import { useState } from "react";
import Swal from "sweetalert2";
function ChangeProfile() {
  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [age, setAge] = useState("");
  const [gender, setGender] = useState("");
  const [city, setCity] = useState("");
  const navigate = useNavigate();
  const changeProfile = async (e) => {
    e.preventDefault();
    const cookeis = new Cookies();
    const token = await cookeis.get("token");
    try {
      const { data } = await axios.put(
        "http://kzico.runflare.run/user/change-profile",
        {
          firstname: firstname,
          lastname: lastname,
          gender: gender,
          age: age,
          city: city,
        },
        {
          headers: {
            authorization: `Bearer ${token}`,
          },
        }
      );
      Swal.fire({
        icon: "success",
        text: "Successfully ChangeProfile",
      });
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: error.message,
      });
    }
  };

  return (
    <div>
      <Header />
      <div className="container-1">
        <div className="back-1">
          <Button
            variant="contained"
            className="button-e"
            onClick={() => navigate("/setting/changeProfile")}
          >
            Change Profile
          </Button>
          <Button
            style={{ background: "#e71d1d" }}
            className="button-e"
            variant="contained"
            onClick={() => navigate("/setting/changePassword")}
          >
            Change Password
          </Button>
          <Button
            style={{ background: "#f9b943" }}
            className="button-e"
            variant="contained"
            onClick={() => navigate("/setting/uploadAvatar")}
          >
            Upload Avatar
          </Button>
        </div>
        <div className="back-2" onSubmit={changeProfile}>
          <div className="back-2-p">
            <div className="col-md-6">
              <input
                type="text"
                className="form-control input-control"
                placeholder="FirstName"
                onChange={(e) => setFirstname(e.target.value)}
                value={firstname}
                style={{ marginBottom: "4px" }}
              />
            </div>
            <div className="col-md-6">
              <input
                type="text"
                className="form-control input-control"
                onChange={(e) => setLastname(e.target.value)}
                value={lastname}
                placeholder="LastName"
                style={{ marginBottom: "4px" }}
              />
            </div>
            <div className="col-md-6">
              <input
                type="text"
                className="form-control input-control"
                onChange={(e) => setGender(e.target.value)}
                value={gender}
                placeholder="Gender"
                style={{ marginBottom: "4px" }}
              />
            </div>
            <div className="col-md-6">
              <input
                type="text"
                className="form-control input-control"
                placeholder="Age"
                onChange={(e) => setAge(e.target.value)}
                value={age}
                style={{ marginBottom: "4px" }}
              />
            </div>
            <div className="col-md-6">
              <input
                type="text"
                className="form-control input-control"
                onChange={(e) => setCity(e.target.value)}
                value={city}
                placeholder="City"
              />
            </div>
          </div>
          <Button className="Save" onClick={changeProfile} variant="contained">
            Save
          </Button>
        </div>
      </div>
    </div>
  );
}

export default ChangeProfile;
