import Header from "../Header";
import "./address.css";
import { Button } from "@mui/material";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { Cookies } from "react-cookie";
import Swal from "sweetalert2";
import { useContext, React, useState } from "react";
import { CartContext } from "../CartContext";
import logo from "./logo.png";
import cartaddress from "./cartaddress.jpg";

function Address() {
  const navigate = useNavigate();
  const [address, setAddress] = useState();
  const [city, setCity] = useState();
  const [postalCode, setPostalCode] = useState();
  const [phone, setPhone] = useState();
  const [payment, setPayment] = useState();
  const [shipping, setShipping] = useState();
  const cart = useContext(CartContext);
  const productCount = cart.items.reduce(
    (sum, product) => sum + product.countInStock,
    0
  );

  const submit = async (e) => {
    let cartProduct = JSON.parse(localStorage.getItem("cartProducts"));
    const reformattedData = cartProduct.map((data) => {
      return {
        product: data._id,
        qty: data.countInStock,
      };
    });
    e.preventDefault();
    try {
      const cookeis = new Cookies();
      const token = await cookeis.get("token");
      const { data } = await axios.post(
        "http://kzico.runflare.run/order/submit",
        {
          orderItems: reformattedData,
          shippingAddress: {
            address: address,
            city: city,
            postalCode: postalCode,
            phone: phone,
          },
          paymentMethod: payment,
          shippingPrice: shipping,
          totalPrice: cart.getTotalCost(),
        },
        {
          headers: {
            authorization: `Bearer ${token}`,
          },
        }
      );
      Swal.fire({
        icon: "success",
        text: "You have successfully purchased",
      });
      localStorage.removeItem("cartProducts");
      navigate("/");
      window.setTimeout(function () {
        window.location.reload();
      }, 2000);
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: error.response.data.message,
      });
    }
  };
  return (
    <div>
      <Header />
      <div onSubmit={submit} className="body">
        <header>
          {/* <div className="d-flex justify-content-center align-items-center pb-3">
          <div className="px-sm-5 px-2 active">
            SHOPPING CART
            <span className="fas fa-check"></span>
          </div>
          <div className="px-sm-5 px-2">CHECKOUT</div>
          <div className="px-sm-5 px-2">FINISH</div>
        </div> */}
        </header>
        <div className="wrapper" style={{ marginLeft: "5%" }}>
          <div className="h5 large"> ADDERSS</div>
          <div className="row">
            <div className="col-lg-6 col-md-8 col-sm-10 offset-lg-0 offset-md-2 offset-sm-1">
              <div className="mobile h5"> Address</div>
              <div id="details" className="bg-white rounded pb-5">
                <form>
                  {/*     <div className="form-group">
                    <label className="text-muted">Name</label>
                    <input
                      type="text"
                      defaultValue="David Smith"
                      className="form-control"
                    />
                  </div> */}

                  <div className="row">
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label>City</label>
                        <div className="d-flex jusify-content-start align-items-center rounded p-2">
                          <input
                            className="white-class"
                            type="text"
                            defaultValue={city}
                            onChange={(e) => setCity(e.target.value)}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label>Zip code</label>
                        <div className="d-flex jusify-content-start align-items-center rounded p-2">
                          <input
                            className="white-class"
                            type="text"
                            defaultValue={postalCode}
                            onChange={(e) => setPostalCode(e.target.value)}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label>Address</label>
                        <div className="d-flex jusify-content-start align-items-center rounded p-2">
                          <input
                            className="white-class"
                            type="text"
                            defaultValue={address}
                            onChange={(e) => setAddress(e.target.value)}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label>Phone</label>
                        <div className="d-flex jusify-content-start align-items-center rounded p-2">
                          <input
                            className="white-class"
                            type="text"
                            defaultValue={phone}
                            onChange={(e) => setPhone(e.target.value)}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="form-group">
                    <label className="text-muted">Payment Method</label>
                    <select
                      defaultValue={payment}
                      onChange={(e) => setPayment(e.target.value)}
                      className="white-class"
                    >
                      <option defaultValue="ins">Select</option>
                      <option defaultValue="usa">Online</option>
                      <option defaultValue="usf">Cash</option>
                      <option defaultValue="ind">Installments</option>
                    </select>
                  </div>
                  <div className="form-group">
                    <label className="text-muted">
                      Shipping price is in dollars, Fast delivery and Normal
                      delivery
                    </label>
                    <select
                      defaultValue={shipping}
                      onChange={(e) => setShipping(e.target.value)}
                      className="white-class"
                    >
                      <option defaultValue="ind">Select</option>
                      <option defaultValue="usf">10</option>
                      <option defaultValue="use">5</option>
                    </select>
                  </div>
                  <label>Country</label>
                  <select className="white-class" name="country" id="country">
                    <option defaultValue="usa">IRAN</option>
                    <option defaultValue="usa">USA</option>
                    <option defaultValue="ind">INDIA</option>
                  </select>
                </form>
              </div>
              {/* <input type="checkbox" />
              <label>Shipping address is same as </label> */}
              <div id="address" className="bg-light rounded mt-3">
                <div className="h5 font-weight-bold text-primary">
                  Shopping Address
                </div>
                <div className="d-md-flex justify-content-md-start align-items-md-center pt-3">
                  <div className="mr-auto">
                    <b>Home Address</b>
                    <p className="text-justify text-muted">542 W.14th Street</p>
                    <p className="text-uppercase text-muted">KARAJ</p>
                  </div>
                  {/*   <div className="rounded py-2 px-3" id="register">
                    <a href="#">
                      <b>Register Now</b>
                    </a>
                    <p className="text-muted">
                      Create account to have multiple address saved
                    </p>
                  </div> */}
                </div>
              </div>
            </div>
            <div className="col-lg-6 col-md-8 col-sm-10 offset-lg-0 offset-md-2 offset-sm-1 pt-lg-0 pt-3">
              <div id="cart" className="bg-white rounded">
                <div className="d-flex justify-content-between align-items-center">
                  <div className="h6">Cart Summary</div>
                  <div className="h6">
                    <a onClick={() => navigate("/cart")}>Edit</a>
                  </div>
                </div>
                <div className="d-flex jusitfy-content-between align-items-center pt-3 pb-2 border-bottom">
                  <div className="item pr-2">
                    <img src={cartaddress} alt="" width="120" height="70" />
                    <div className="number">{productCount}</div>
                  </div>
                  <div className="d-flex flex-column px-3">
                    <b className="h5"></b>
                    <p href="#" className="h5 text-primary">
                      <small style={{ textAlign: "center" }}>
                        You can add your gift card or promotional card in the
                        section below
                      </small>
                    </p>
                  </div>
                </div>
                <div className="my-3">
                  <input
                    type="text"
                    className="w-100 form-control text-center"
                    placeholder="Gift Card or Promo Card"
                  />
                </div>
                <div className="d-flex align-items-center">
                  <div className="display-5">Quantity : </div>
                  <div
                    className="ml-auto font-weight-bold"
                    style={{ marginLeft: "10px" }}
                  >
                    {productCount}
                  </div>
                </div>
                {/* <div className="d-flex align-items-center py-2 border-bottom">
                  <div className="display-5">Shipping</div>
                  <div className="ml-auto font-weight-bold">$12.9</div>
                </div> */}
                <div className="d-flex align-items-center py-2">
                  <div className="display-5">Total</div>
                  <div className="ml-auto d-flex">
                    <div className="text-primary text-uppercase px-3">usd</div>
                    <div className="font-weight-bold">
                      ${cart.getTotalCost().toFixed(2)}
                    </div>
                  </div>
                </div>
              </div>
              {/*      <p className="text-muted">Need help with an order?</p>
              <p className="text-muted">
                <a href="#" className="text-danger">
                  Hotline:
                </a>
                +314440160 (International)
              </p> */}
              <div className="h4 pt-3">
                <span className="fas fa-shield-axt-primary pr-2"></span>
                Security of your shopping
              </div>
              <div id="summary" className="bg-white rounded py-2 my-4">
                <div className="table-responsive">
                  <img src={logo} />
                </div>
                <div className="border-top py-2 d-flex align-items-center ml-2 font-weight-bold">
                  <div style={{ marginLeft: "55px" }}>
                    Shop with confidence at Infinity Store
                  </div>
                  {/*  <div className="ml-auto text-primary"> USD</div>
                  <div className="px-2">${cart.getTotalCost().toFixed(2)}</div> */}
                </div>
              </div>
              <div className="row pt-lg-3 pt-2 buttons mb-sm-0 mb-2">
                <div className="col-md-6">
                  <Button variant="contained" onClick={() => navigate("/cart")}>
                    Back to cart
                  </Button>
                </div>
                <div className="col-md-6 pt-md-0 pt-3">
                  <Button
                    onClick={submit}
                    style={{ background: "#218838" }}
                    variant="contained"
                  >
                    Shopping
                  </Button>
                </div>
              </div>
            </div>
          </div>
          <p className="text-muted">
            <span style={{ color: "white" }}>Your information is save</span>
          </p>
        </div>
      </div>
    </div>
  );
}

export default Address;
