import Button from "react-bootstrap/Button";
import { CartContext } from "./CartContext";
import { useContext } from "react";
import { getProductData } from "./ProductTest";
function CartProduct(props) {
  const cart = useContext(CartContext);
  const _id = props._id;
  const countInStock = props.countInStock;
  const productData = getProductData(_id);

  return (
    <>
      <div className="order-card">
        <div className="order-card-1">
          <img src={productData.image} className="photo-item" alt={""} />
          <div className="order-item">
            {productData.name}
            <div className="order-total">
              <p> {countInStock} total</p>
              <p className=" order-total-p">
                Price: ${(countInStock * productData.price).toFixed(2)}
              </p>
            </div>
          </div>
        </div>
        <Button
          className="remove-cart"
          color="error"
          size="sm"
          onClick={() => cart.deleteFromCart(_id)}
        >
          Remove
        </Button>
      </div>
    </>
  );
}

export default CartProduct;
