import {
  oneproductLoading,
  oneproductSuccess,
  oneproductFailed,
  productLoading,
  productSuccess,
  productFailed,
  allorderLoading,
  allorderSuccess,
  allorderFailed,
  oneorderLoading,
  oneorderSuccess,
  oneorderFailed,
  shippingAddressLoading,
  shippingAddressSuccess,
  shippingAddressFailed,
} from "../constants";
export const product = (
  state = { data: [], error: "", loading: false },
  { type, payload }
) => {
  switch (type) {
    case productLoading:
      return payload;
    case productSuccess:
      return payload;
    case productFailed:
      return payload;

    default:
      return state;
  }
};
